import React, { Component } from 'react';
import Tess from './Tess'
import Time from './Time'
import Weather from './Weather'
import BusInfo from './BusInfo'
import '../styles/App.css';

class App extends Component {
  render() {
    return (
      <div className='App'>
        <div className='top'>
          <Weather />
          <Time />
        </div>
        <div className='center'>
          <Tess />
        </div>
        <BusInfo />
      </div>
    );
  }
}

export default App;



